@import '../../../scss/mixins';

.wrapper {
  .cardTitle {
    margin: 0;
    font-size: 33px;
    text-align: center;
    white-space: pre-line;
    @media screen and (max-width: 860px) {
      font-size: 39px;
    }
    @media screen and (max-width: 480px) {
      font-size: 31px;
    }
    @media screen and (max-width: 360px) {
      font-size: 29px;
    }
    @media screen and (max-width: 340px) {
      font-size: 27px;
    }
  }
  .content {
    margin: 16px 0;
    @include hbIntroText;
    text-align: center;
  }
  .image {
    margin-bottom: 16px;
    background: url('../../../assets/images/success_signup.gif');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 260px;
    width: 100%;
  }
  .btnSubmit {
    @include hbButtonText;
    width: 100%;
    height: 45px;
    padding: 0;
    border-radius: 12px;
  }
}
